import { Badge } from "antd";
import { Link } from "react-router-dom";

export function getItem(label, key, icon, children) {
  return {
    key,
    icon,
    children,
    label,
  };
}

export function getLabel(label, count, path) {
  return path ? (
    <Link
      to={path}
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        paddingRight: "10px",
      }}
    >
      {label} <Badge count={count} />
    </Link>
  ) : (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        paddingRight: "10px",
      }}
    >
      {label} <Badge count={count} />
    </div>
  );
}
