// basic libraries
import { useState } from "react";

// installed libraries
import { Layout, Result } from "antd";
import { Route, BrowserRouter as Router, Routes } from "react-router-dom";
// styles
import "./App.css";
import "antd/dist/antd.min.css";
import "./assets/styles/style.css";
import "./assets/styles/utils.css";
import "./assets/styles/ispolnitel.css";

/*-------------------from local files-------------------------*/
// layout
import SiderBar from "./layouts/SiderBar";
// import SiderBar1 from "./layouts/SiderBar1";
import HeaderComponent from "./layouts/Header";

// common
import Messages from "./components/messages/Messages";
import Settings from "./components/Settings";
import Archive from "./components/Archive";
import BulimBoshligi from "./routers/BulimBoshligi";
import HomePage from "./components/HomePage";
import Ispolnitel from "./routers/Ispolnitel";

// user...
const user = "user";
function App() {
  // 1 -> nachalnik otdel
  // 2 -> ispolnitel geolog
  // 3 -> nachalnik parti
  // 4 -> laboratoriya
  // 5 -> slujba glavnogo geologa
  const [collapsed, setCollapsed] = useState(false);
  return (
    <Layout style={{ position: "relative" }}>
      <Router>
        <SiderBar
          collapsed={collapsed}
          setCollapsed={setCollapsed}
          user={user}
        />
        <Layout>
          <HeaderComponent collapsed={collapsed} setCollapsed={setCollapsed} />
          <Layout>
            <Routes>
              {/*---------- Routers ---------------------*/}
              <Route element={<BulimBoshligi />} path="/bulim_boshligi/*" />

              {/*-------------------ispolnitel-------------------*/}

              <Route element={<Ispolnitel />} path="/user/*" />
              {/*-------------------- common routers ---------------------*/}
              <Route element={<HomePage />} path="/" />
              <Route element={<Messages />} path="/xabarlar" />
              <Route element={<Settings />} path="/sozlanmalar" />
              <Route element={<Archive />} path="/arxiv" />
              <Route
                element={
                  <Result
                    status={404}
                    title="404"
                    subTitle="sahifa topilmadi"
                  />
                }
                path="*"
              />
            </Routes>
          </Layout>
        </Layout>
      </Router>
    </Layout>
  );
}

export default App;
